<template>
    <div>
        <div class="topTitle">
            <div class="leftTitle">命令</div>
            <div class="rightButton">
                <el-button type="primary" size="mini" @click="addCk(1)">
                    添加命令</el-button>
            </div>
        </div>

        <div class="mainMseeage">
            <el-form :inline="true" :model="formInline" class="searchForm">
                <el-form-item label="command">
                    <el-input v-model="formInline.command" placeholder="command" clearable></el-input>
                </el-form-item>
                <el-form-item label="function">
                    <el-input v-model="formInline.function" placeholder="function" clearable></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </el-form>

            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="command" label="命令" width="" align="center">
                </el-table-column>
                <el-table-column prop="function" label="功能" width="" align="center">
                </el-table-column>
                <el-table-column prop="reply" label="回复" align="center"></el-table-column>
                <el-table-column prop="isBuiltIn" label="是否内置" align="center">
                    <template slot-scope="scoped">
                        {{scoped.row.isBuiltIn | isBuiltInType}}
                    </template>
                </el-table-column>
                <el-table-column prop="createdTime" label="创建时间" align="center"></el-table-column>
                <el-table-column prop="updatedTime" label="修改时间" align="center"></el-table-column>
                <el-table-column label="操作" fixed="right" align="center">
                    <template slot-scope="scoped">
                        <el-button type="text" :disabled="scoped.row.isBuiltIn==1" @click="addCk(2,scoped.row)">修改
                        </el-button>
                        <el-button type="text" :disabled="scoped.row.isBuiltIn==1" :loading="scoped.row.deleteLoading"
                            @click="deleteCd(scoped.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>


        <!-- 添加/修改命令 -->
        <el-dialog :title="boxName" :visible.sync="addVisible" width="30%" @close="addClosed('addForm')">
            <el-form :model="addForm" :rules="addrules" ref="addForm">
                <el-form-item label="命令" prop="command">
                    <el-input v-model="addForm.command" clearable></el-input>
                </el-form-item>
                <el-form-item label="功能" prop="function">
                    <el-input v-model="addForm.function" clearable></el-input>
                </el-form-item>
                <el-form-item label="回复内容" prop="reply">
                    <el-input v-model="addForm.reply" clearable></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false">取 消</el-button>
                <el-button type="primary" :loading="sureLoading" @click="sureAdd('addForm')">确 定</el-button>
            </span>
        </el-dialog>


    </div>
</template>

<script>
import { Message } from 'element-ui'
import { onScopeDispose } from 'vue'
import { queryCommand, addCommand, updateCommand, deleteCommand } from '../axiosRequest/allRequest'
export default {

    filters: {
        isBuiltInType(value) {
            const obj = {
                0: '否',
                1: '是'
            }
            return obj[value]
        }
    },

    data() {
        return {
            formInline: {},
            tableData: [],
            addVisible: false,
            boxName: '新增命令',
            diType: '1',
            addForm: {
                command: '',
                function: '',
                reply: '',
                id: ''
            },
            addrules: {
                command: [
                    { required: true, message: '请输入命令', trigger: 'blur' },
                ],
                function: [],
                reply: []
            },
            sureLoading: false
        }
    },

    created() {
        this.onSubmit()
    },

    methods: {
        onSubmit() {
            queryCommand(this.formInline).then((res) => {
                if (res.code == 200) {
                    let a = JSON.parse(JSON.stringify(res.data))
                    a.forEach((item, index) => {
                        this.$set(item, 'deleteLoading', false)
                    })
                    this.tableData = a
                } else {
                    Message.error(res.message)
                }
            })
        },

        addCk(type, row) {
            this.diType = type
            if (type == '1') {
                this.boxName = '新增命令'
            } else {
                this.boxName = '修改命令'
                this.addForm.command = row.command
                this.addForm.function = row.function
                this.addForm.reply = row.reply
                this.addForm.id = row.id
            }

            this.addVisible = true
        },

        sureAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = this.addForm
                    this.sureLoading = true
                    if (this.diType == '1') {
                        //新增
                        delete param.id
                        addCommand(param).then((res) => {
                            this.sureLoading = false
                            if (res.code == 200) {
                                Message.success('新增成功')
                                this.addVisible = false
                                this.onSubmit()
                            } else {
                                Message.error(res.message)
                            }
                        })
                    } else {
                        //修改
                        updateCommand(param).then((res) => {
                            this.sureLoading = false
                            if (res.code == 200) {
                                Message.success('修改成功')
                                this.addVisible = false
                                this.onSubmit()
                            } else {
                                Message.error(res.message)
                            }
                        })
                    }


                } else {
                    return false;
                }
            });
        },

        addClosed(formName) {
            this.$refs[formName].resetFields();
        },
        deleteCd(row) {
            this.$confirm(`是否确认删除${row.command}命令`, '删除命令', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.tableData.forEach((item, index) => {
                    if (item.id == row.id) {
                        this.$set(item, 'deleteLoading', true)
                    }
                })
                deleteCommand([row.id]).then((res) => {
                    if (res.code == 200) {
                        Message.success('删除成功')
                        this.onSubmit()
                    } else {
                        Message.error(res.message)
                    }
                })
            })
        }
    }
}
</script>

<style lang="less" scoped>

</style>